@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;400;700;900&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Lato';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	scroll-behavior: smooth;
	
}

body,
html {
    height: 100%;
	background-color: #000;
}

#particles-js canvas {
    display: block;
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    transition: opacity .8s ease, -webkit-transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease;
    transition: opacity .8s ease, transform 1.4s ease, -webkit-transform 1.4s ease
}

#particles-js {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 0;
    top: 0;
    left: 0
}

.main {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 100px;
	justify-content: center;
	align-items: center;
	width:100%;
	padding: 0;
}

#btn {
	height: 30px;
	width: 100px;
	font-weight: bold;
}


hr {
	background-color: currentColor;
	min-width: 50%;
	height: 3px;
	border: none;
	margin: auto;
	position: relative;	
	
  }

  hr:before {
	font-family: cursive;
	content: '№❦';
	display: block;
	width: 20%;
	padding: 0.3em;
	font-size: 2em;
	text-align: center;
	position: absolute;
	top: 0;
	left: 50%;
	background-color: #f2f2f2;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	
  }

/* Fix for flickering carousel */                                                      
.carousel-inner .carousel-item {
	opacity: 0;
	top: 0;
	left: 0;
	width: 100%;
	display: block;
	position: absolute;
  }
  .carousel-inner .carousel-item:first-of-type {
	position: relative;
  }
  .carousel-inner .active {
	opacity: 1;
  }
  /* End of fix */


.socialHover:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
